
import { defineComponent, ref } from 'vue';

export default defineComponent({
  props: {
    caption: {
      type: String,
      required: true
    }
  },
  setup() {
    let isCollapsed = ref<boolean>(true);

    const toggle = (): void => {
      isCollapsed.value = !isCollapsed.value;
    };

    return { isCollapsed, toggle };
  }
});
