
import { defineComponent } from 'vue';

export default defineComponent({
  props: {
    ctype: {
      type: String,
      default: 'codehash'
    }
  },
  setup(props, { emit }) {
    const closeModal = () => {
      emit('update:is-open', false);
    };

    const agree = () => {
      emit('agree', true);
    };

    return {
      closeModal,
      agree
    };
  }
});
